.create{
    cursor: pointer;
    background: white;
    border-radius: 1rem;
    border: solid 0.094rem #9e9e9e;
    color: #757575;
    height: 45px;
    width: 500px;
    box-shadow: 0px 0.313px 3.125rem rgba(32, 35, 41, 0.25);
    margin-right: auto;
    margin-left: auto;
}

.loader {
    display: block;
    --height-of-loader: 4px;
    --loader-color: #0071e2;
    width: 130px;
    height: var(--height-of-loader);
    border-radius: 30px;
    background-color: rgba(0,0,0,0.2);
    position: relative;
    margin-left: 55rem;
  }
  
  .loader::before {
    content: "";
    position: absolute;
    background: var(--loader-color);
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    border-radius: 30px;
    animation: moving 1s ease-in-out infinite;
    ;
  }
  
  @keyframes moving {
    50% {
      width: 100%;
    }
  
    100% {
      width: 0;
      right: 0;
      left: unset;
    }
  }

  .arrow {
    position: absolute;
    top: 400px;
    left: 50%;
    transform: translate(-100%, -100%);
    transform: rotate(180deg);
    cursor: pointer;
}

.arrow span {
    display: block;
    width: 1.5vw;
    height: 1.5vw;
    border-bottom: 5px solid rgb(145, 145, 145);
    border-right: 5px solid rgb(145, 145, 145);
    transform: rotate(45deg);
    margin: -10px;
    animation: animate 2s infinite;
}

.arrow span:nth-child(2) {
    animation-delay: -0.2s;
}

.arrow span:nth-child(3) {
    animation-delay: -0.4s;
}

@keyframes animate {
    0% {
        opacity: 0;
        transform: rotate(45deg) translate(-20px, -20px);
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        transform: rotate(45deg) translate(20px, 20px);
    }
}

.arrow_2 {
  position: absolute;
  bottom: 3.5%;
  right: 12rem;
  transform: translate(-100%, -100%);
  transform: rotate(-90deg);
  cursor: default
  ;
}

.arrow_2 span {
  display: block;
  width: 1.5vw;
  height: 1.5vw;
  border-bottom: 5px solid rgb(145, 145, 145);
  border-right: 5px solid rgb(145, 145, 145);
  transform: rotate(45deg);
  margin: -10px;
  animation: animate 2s infinite;
}

.arrow_2 span:nth-child(2) {
  animation-delay: -0.2s;
}

.arrow_2 span:nth-child(3) {
  animation-delay: -0.4s;
}

@keyframes animate {
  0% {
      opacity: 0;
      transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
      opacity: 1;
  }
  100% {
      opacity: 0;
      transform: rotate(45deg) translate(20px, 20px);
  }
}