.TodoSearch-group {
  position: relative;
 }

.TodoSearch {
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  border-radius: 1rem;
  border: solid 1.5px #9e9e9e;
  background: white;
  padding: 1rem;
  padding-right: 10rem;
  padding-left: 10rem;
  font-size: 2rem;
  color: black;
  transition: border 150ms cubic-bezier(0.4,0,0.2,1);
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  display: flex;
  }

  .user-label {
    position: absolute;
    left: 15px;
    color: #e8e8e8;
    pointer-events: none;
    transform: translateY(1rem);
    transition: 150ms cubic-bezier(0.4,0,0.2,1);
   }

  .TodoSearch:focus, TodoSearch:valid {
    outline: none;
    border: 1.5px solid #1a73e8;
   }

   .TodoSearch:focus ~ label, TodoSearch:valid ~ label {
    transform: translateY(-50%) scale(0.8);
    background-color: #212121;
    padding: 0 .2em;
    color: #2196f3;
   }
